// @flow
import React, { Component } from 'react';
import { observer } from 'mobx-react';
import Unit from "../lib/Unit"

@observer
export default class Line extends Component {

  props: {
    unit: Unit,
    offset: number,
    color: string
  }

  render() {
    const { unit, offset } = this.props;
    let targetCoordinates = unit.targetCoordinates;
    if (!unit.targetCoordinates) return null;
    let classNames = ["line", unit.team.class]
    if (unit.showDistanceRings) classNames.push("selected")
    return ( 
      <line 
        className={classNames.join(" ")}
        x1={ unit.x + offset } 
        x2={ targetCoordinates.x + offset} 
        y1={ unit.y } 
        y2={ targetCoordinates.y + offset} 
        />
    )
  }
}



