// @flow
import { observable, action, computed } from 'mobx'
import _ from 'lodash'
import Victor from 'victor' // Vector processing

import { createUnit } from "./Unit"
import Bullet from "./Bullet"
import Board from "./Board"
import Unit from "./Unit"
import { createFiringPattern } from "./FiringPattern"
import { distances, actionTypes, directions, gameParams } from "./gameInfo"
import { metersPerSecondToPixelsPerTick, metersToPixel, pixelsPerTick, millisecondsToTick } from "./TickUtils"

class Game {
    board: Board
    tickCount: number
    ticker: number
    gameConfig: Object
    millisecondsPerTick: number
    thisLoop: Date 
    lastLoop: Date
    targetFps: number

    @observable tickCount: number;
    @observable board: Board;
    @observable fps: number = 0;

    @computed get timeElapsedInSeconds(): number {
        return this.tickCount < 0 ? 0 : Math.floor(this.tickCount * gameParams.millisecondsPerFrame.normal / 1000)
    }

    constructor(config: Object, startAtTick: ?number) {
        this.targetFps = 50
        this.millisecondsPerTick = gameParams.millisecondsPerFrame.normal
        this.gameConfig = config
        this.tickCount = -1
        this.ticker = 0
        this.board = new Board()
        this.lastLoop = new Date();
        this.thisLoop = new Date();
        // Place units relative to the center of the board
        const maxX = _.max(_.map(this.gameConfig.units, u => u.x));
        const maxY = _.max(_.map(this.gameConfig.units, u => u.y));
        const originX = (this.board.width / 2.0) - (maxX / 2.0);
        const originY = (this.board.height / 2.0) - (maxY / 2.0) - 10;
        for (let unitConfig of this.gameConfig.units) {
            let unit = createUnit({ ...unitConfig, ...{ x: unitConfig.x + originX, y: unitConfig.y + originY } })
            this.board.addUnit(unit)
        }

        if(startAtTick && startAtTick > 0) {
            this.jumpForwardToTick(startAtTick)
        }
    }

    play = () => { this.changeSpeed(1) }
    fastMotion = () => { this.changeSpeed(4) }
    slowMotion = () => { this.changeSpeed(0.25) }

    changeSpeed(factor: number) {
        this.pause()
        this.start(factor)
    }

    pause = () => {
        clearInterval(this.ticker);
        this.ticker = 0;
    }

    @action jumpForwardToTick(tickNumber: number): void {
        while (this.tickCount < tickNumber) {
            this.tick()
        }
    }

    start(factor: number = 1) {
        this.currentFactor = factor
        this.startTime = new Date().getTime()
        this.filterStrength = 30;
        this.frameTime = 0
        this.lastLoop = new Date() 
        let millisecondsPerFrame = 1000.0 / this.targetFps
        let ticksPerFrame = Math.round((1000 / this.millisecondsPerTick) / this.targetFps * this.currentFactor)
        if (!this.ticker) {
            this.ticker = setInterval(
                () => { 
                    if(this.adjustFramerate()) {
                        this.changeSpeed(this.currentFactor)
                        return
                    } else {
                        this.executeTick(ticksPerFrame)
                    }
                }
            , millisecondsPerFrame);
        }
    }

    // Automatically lower frame rate
    adjustFramerate():boolean {
        let adjusted: boolean = false;
        if (((new Date()).getTime() - this.startTime) > 5000) {
            if (this.targetFps > 33 && this.fps < 45) {
                this.targetFps = 33
                adjusted = true
            }
            if (!(this.targetFps > 20) && this.fps < 25) {
                this.targetFps = 20
                adjusted = true
            }
        }
        if (adjusted) this.startTime = new Date().getTime()
        return adjusted;
    }

    // Run [ticksToRun] number of ticks per rendered frame
    @action executeTick(ticksToRun: number) {
        var thisFrameTime = (this.thisLoop=new Date()) - this.lastLoop;
        this.frameTime += (thisFrameTime - this.frameTime) / this.filterStrength;
        this.lastLoop = this.thisLoop;
        this.fps = (1000/this.frameTime).toFixed(1)

        // execute ticks
        for (var i = 0; i < ticksToRun; i++) {
            this.tick()
        }
    }

    tick= () => {
        this.tickCount += 1;

        // Todo: bullets shouldn't move before unit actions performed
        this.board.bullets.forEach(bullet => this.moveBullet(bullet))

        // 1. Generate modifier function for every unit, then
        // 2. Execute the modifier function
        // Modifier functions cannot reference unit data from previous ticks.
        // This ensures that modifications are only calculated based on data from previous tick
        this.board.units
            .map(unit => {
                // Run the unit's AI and set the unit.nextAction property for each unit
                this.fetchNextAction(unit);
                return unit
            })
            .map(unit => { 
                // Set the currentAction to the nextAction set in the previous step.
                // This must be done only after all the AIs steps have been fetched
                // , because the AIs use the currentAction to see what other units are doing
                unit.currentAction = unit.nextAction
                return unit.performAction()
            })
            .forEach(f => f())

        this.regenerateShield()
        this.setDistanceToSelectedUnit()
    }

    clickOnUnit = (unit: Unit) => {
        console.log(unit)
        this.board.clickOnUnit(unit)
        // Manually run this if the game isn't running
        if (!this.isGameRunning()) this.setDistanceToSelectedUnit()
    }

    clickOnBoard = () => {
        // console.log("BOARD CLICK")
        // this.board.clearDistanceRings()
    }

    isGameRunning(): boolean {
        return this.ticker !== 0
    }

    selectedUnit(): ?Unit {
        return this.board.units.filter(u => u.showDistanceRings)[0]
    }

    // If a unit is selected then set the distance to the selected unit for each unit
    // so we can indicate it in the view
    setDistanceToSelectedUnit() {
        let unit = this.selectedUnit()
        if (unit) {
            this.board.units
                .forEach(u => {
                    u.distanceToSelectedUnit = unit.distance(u)
                })
        } else {
            // if selected unit has died then reset all distances
            this.board.units.forEach(u => u.hideDistanceRings())
        }
    }

    regenerateShield() {
        const ticksToRegenerateShield = millisecondsToTick(gameParams.shieldRechargeMilliseconds)
        this.board.units
            .filter(u => u.lastDamageTick &&
                u.shield < u.unitType.shield &&
                u.lastDamageTick < (this.tickCount - ticksToRegenerateShield))
            .forEach(u => { 
                let shieldIncrease = u.unitType.shield / ticksToRegenerateShield
                u.shield += Math.min(shieldIncrease, u.unitType.shield - u.shield)
            })
    }

    fetchNextAction(unit: Unit) {
        if (this.tickCount % millisecondsToTick(250) === 0) {
            // Find next action from the unit's AI
            const newAction = unit.ai.findNextAction(unit, this.board)

            switch (newAction.actionType) {
                case actionTypes.move:
                    unit.nextAction = newAction
                    unit.performAction = this.moveUnit(unit)
                    break;
                case actionTypes.attack:
                    // If the unit is already attacking the unit then do not initiate a new attack
                    if (unit.currentAction.actionType === actionTypes.attack &&
                        newAction.target_unit === unit.currentAction.target_unit) {
                        return
                    }
                    unit.nextAction = newAction
                    unit.performAction = createFiringPattern(
                        unit,
                        newAction.target_unit,
                        this,
                        gameParams.millisecondsPerFrame.normal,
                        this.createBullet
                    )
                    break;
            }
        }
    }

    moveUnit(unit: Unit) {
        return () => {
            // Kill units when they thit the wall
            if (unit.x < 20 || unit.y < 20 || unit.x > this.board.width - 20 || unit.y > this.board.height - 20) {
                return () => this.killUnit(unit)
            }

            const { x: sourceX, y: sourceY,
                currentAction: { direction, target_unit } } = unit;

            // Do nothing if the target unit is dead
            if (!target_unit || !this.unitIsAlive(target_unit)) { return () => { } }

            const { x: targetX, y: targetY } = target_unit
            const diff = new Victor(targetX - sourceX, targetY - sourceY).normalize();
            const angle = Math.abs(diff.angleDeg()) - 90;
            const diffX = (diff.x * pixelsPerTick(unit.unitType.travelSpeed))
            const diffY = (diff.y * pixelsPerTick(unit.unitType.travelSpeed))

            return () => {
                if (direction === directions.towards) {
                    unit.x += diffX
                    unit.y += diffY
                } else if (direction === directions.away) {
                    unit.x -= diffX
                    unit.y -= diffY
                }
                unit.rotation = _.round(angle);
                unit.shootingLevel = 0
            }
        }
    }

    createBullet = (sourceUnit: Unit, targetUnit: Unit) => {
        const diff = new Victor(targetUnit.x - sourceUnit.x, targetUnit.y - sourceUnit.y).normalize();

        // Calculate accuracy
        const distanceType = sourceUnit.distance(targetUnit)
        let accuracy:number;
        if (distanceType === distances.long)
            accuracy = sourceUnit.unitType.attackRange.long
        else if (distanceType === distances.medium)
            accuracy = sourceUnit.unitType.attackRange.medium
        else if (distanceType === distances.short)
            accuracy = sourceUnit.unitType.attackRange.short
        else {
            // throw new Error("This should not happen")
            accuracy = 0
        }

        let bulletVelocityPixelsPerTick = metersPerSecondToPixelsPerTick(gameParams.bulletVelocity)
        const bullet = new Bullet({
            accuracy: accuracy,
            sourceUnit, targetUnit,
            sourceCoordinate: { x: sourceUnit.x, y: sourceUnit.y },
            currentCoordinate: { x: sourceUnit.x, y: sourceUnit.y },
            willHit: true,
            angle: Math.abs(diff.angleDeg()),
            velocity: { x: diff.x * bulletVelocityPixelsPerTick, y: diff.y * bulletVelocityPixelsPerTick},
            // Apply random offset to each bullet
            displayOffset: { x: parseInt(Math.random() * 20) - 10, y: parseInt(Math.random() * 20) - 10 }
        })
        this.board.bullets.push(bullet)
    }

    unitIsAlive(unit: Unit) {
        return _.includes(this.board.units, unit)
    }

    killUnit(unit: Unit) {
        unit.dead = true; // used when other objects reference this unit
        this.board.units.remove(unit)
    }

    moveBullet(bullet: Bullet) {
        const { currentCoordinate: { x, y } } = bullet;

        // Bullet has left the board
        if (x < 0 || x > this.board.width || y < 0 || y > this.board.height) {
            //this.board.bullets.remove(bullet);
            this.board.removeBullet(bullet);
            return
        }

        bullet.currentCoordinate.x += bullet.velocity.x
        bullet.currentCoordinate.y += bullet.velocity.y

        if (bullet.willHit) {
            if (!this.unitIsAlive(bullet.targetUnit)) {
                // The target unit has been killed
                bullet.willHit = false;
                return;
            }
            const sourcePosition = Victor.fromObject(bullet.sourceCoordinate)
            const currentPosition = Victor.fromObject(bullet.currentCoordinate)
            const unitPosition = Victor.fromObject(bullet.targetUnit)
            const distanceTravelled = sourcePosition.distance(currentPosition)
            const distanceToUnit = sourcePosition.distance(unitPosition)

            if (distanceTravelled >= distanceToUnit) {
                // bullet has just reached the unit
                if (distanceToUnit > metersToPixel(gameParams.largeCircleRadius)) {
                    bullet.willHit = false;
                } else {
                    // update the units hit counter 
                    bullet.targetUnit.hitCounter += bullet.accuracy
                    if (bullet.targetUnit.hitCounter >= 100) {
                        // Bullet has hit the target
                        bullet.targetUnit.hitCounter %= 100
                        bullet.targetUnit.damage(bullet.sourceUnit.unitType.damagePerBullet)
                        // Record time the unit was last damaged - assists with shield regeneration
                        bullet.targetUnit.lastDamageTick = this.tickCount
                        // Remove bullet from board
                        this.board.removeBullet(bullet);
                        if (bullet.targetUnit.hp <= 0) this.killUnit(bullet.targetUnit)
                    } else {
                        // Bullet has missed
                        bullet.willHit = false;
                    }
                }
            }
        }
    }
}

export default Game



