// @flow
import React, { Component } from 'react'
import { observable } from 'mobx'
import { observer } from 'mobx-react'
import './App.css'

import BoardView from "./components/BoardView"
import GameControls from "./components/GameControls"
import Fps from "./components/Fps"
import TimeElapsed from "./components/TimeElapsed"

import Game from "./lib/Game"
import { selectedConfig } from "./maps"

@observer
class App extends Component {

    @observable.ref game: Game;

    constructor(props: Object) {
        super(props);
        this.reset()
    }

    reset = (jumpToTickNumber: ?number) => {
        this.game = new Game(selectedConfig, jumpToTickNumber)
    }

    render() {
        const { board } = this.game
        return (
            <div className="App">
                <BoardView board={board} clickOnBoard={this.game.clickOnBoard} clickOnUnit={this.game.clickOnUnit}>
                    <TimeElapsed seconds={this.game.timeElapsedInSeconds} />
                    <Fps game={this.game} />
                    <GameControls game={this.game} reset={this.reset} />
                </BoardView>
            </div>
        );
    }

    // Resize the board when the screen is resized (mobile changed from portrait to landscape)
    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', () => this.updateWindowDimensions());
    }

    componentWillUnmount() {
        window.removeEventListener('resize', () => this.updateWindowDimensions());
    }

    updateWindowDimensions() {
        this.game.board.width = window.innerWidth
        this.game.board.height = window.innerHeight
    }
}

export default App;



