import React, { Component } from 'react';
import { autorun } from "mobx"
import Victor from "victor"

import { metersToPixel  } from "../lib/TickUtils"
import { gameParams } from "../lib/gameInfo"

function rect(props) {
    const {ctx, x, y, width, height} = props;
    ctx.fillRect(x, y, width, height);
}

class CanvasBullets extends Component {
    componentDidMount() {
        this.componentDidUpdate()
    }

    componentDidUpdate() {
        // clear existing autorun
        if (this.disposer) this.disposer();
        // update bullet positions when mobx values are updated
        this.disposer = autorun(() => {

            const ctx = this.refs.canvas.getContext('2d');
            ctx.fillStyle = "white"
            ctx.clearRect(0, 0, this.props.width, this.props.height);

            if(this.props.bullets.length === 0) return;

            // render each bullet
            this.props.bullets.forEach(b => {
                const { sourceCoordinate: { x: sourceX, y: sourceY }, currentCoordinate: { x, y },
                    displayOffset: { x: offsetX, y: offsetY } } = b
                let s = new Victor(sourceX, sourceY)
                let t = new Victor(x, y)

                let offsetFactor = s.distance(t) / metersToPixel(gameParams.mediumCircleRadius)
                let displayX = Math.round(x + (offsetX * offsetFactor))
                let displayY = Math.round(y + (offsetY * offsetFactor))
                let angle = (new Victor(x-sourceX, sourceY - y)).verticalAngle()
                ctx.fillStyle = b.sourceUnit.team.colour
                ctx.translate(displayX, displayY)
                ctx.rotate(angle);
                rect({ ctx, x: -1.5, y: -3, width: 3, height: 6 });
                ctx.setTransform(1, 0, 0, 1, 0, 0);
            })
        })
    }

    render() {
        const { width, height } = this.props
        return (
            <canvas ref="canvas" className="canvas" width={width} height={height} style={{ position: "absolute", top: 0, left: 0}}></canvas>
        );
    }
}

export default CanvasBullets


